import { Injectable } from '@angular/core';
import {AngularFirestore, AngularFirestoreCollection} from '@angular/fire/firestore';
import {PersonalData} from '../model/personalData';
import {Project} from '../model/project';

@Injectable({
  providedIn: 'root'
})
export class FirebaseService {
private projects: AngularFirestoreCollection<Project>;
  constructor( private angularFirestore: AngularFirestore) {
    this.projects = angularFirestore.collection('projects');
  }

  all () {
    return this.angularFirestore.collection<PersonalData>('about-me').valueChanges();
  }
  synchronizeWithBehance(project: Project): Promise<void> {
    return  this.projects.doc('' + project.id).set(project).catch(console.log);
  }
}
